body {
  color: #e0e0e0;
  background-color: #2b2b2b;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
}

.container {
  text-align: center;
  background-color: #383838;
  border-radius: 10px;
  width: 100%;
  max-width: 400px;
  padding: 30px;
  box-shadow: 0 4px 15px #00000080;
}

h2 {
  color: #fff;
  margin-bottom: 20px;
  font-weight: 300;
}

.form-group {
  text-align: left;
  margin-bottom: 15px;
}

label {
  color: #aaa;
  margin-bottom: 5px;
  font-size: .9em;
  display: block;
}

input {
  color: #fff;
  box-sizing: border-box;
  background-color: #4a4a4a;
  border: 1px solid #5a5a5a;
  border-radius: 5px;
  width: 100%;
  padding: 10px;
  font-size: 1em;
}

input:focus {
  background-color: #505050;
  border-color: #4caf50;
  outline: none;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  width: 100%;
  margin-top: 10px;
  padding: 12px;
  font-size: 1em;
  transition: background-color .3s;
}

button:hover {
  background-color: #45a049;
}

#downloadBtn {
  background-color: #2196f3;
  margin-top: 10px;
  display: none;
}

#downloadBtn:hover {
  background-color: #0b7dda;
}
